import Vue from "vue";
import Router from "vue-router";
import VueAxios from 'vue-axios'
import axios from 'axios'
import Reserva from '@/components/commons/Reserva'
import Ok from '@/components/commons/Ok'
import Ko from '@/components/commons/Ko'
import OtrosFormulario from '@/components/otros/Formulario'
import OtrosDispo from '@/components/otros/Dispo'
import OtrosRates from '@/components/otros/Rates'
import OtrosDetalle from '@/components/otros/Detalle'
import VCalendar from 'v-calendar'
import 'v-calendar/lib/v-calendar.min.css'

Vue.use(Router);
Vue.use(VueAxios, axios)
Vue.use(VCalendar, {
  firstDayOfWeek: 2, // Monday
  locale: globaldata.language,
  showLinkedButtons:true,
  popoverVisibility:'focus'

})


export default new Router({
  routes: [
    {
      path: '/',
      name: 'reserva',
      component: Reserva,
      children: [

        {
          path: '',
          name: 'main',
          component: OtrosFormulario
        },
        {
          path: 'rates',
          name: 'otros_rates',
          component: OtrosRates
        },
        {
          path: 'dispo',
          name: 'otros_dispo',
          component: OtrosDispo
        },

        {
          path: 'detalle',
          name: 'otros_detalle',
          component: OtrosDetalle
        },
        {
          path: 'ok',
          name: 'ok',
          component: Ok
        },
        {
          path: 'ko',
          name: 'ko',
          component: Ko
        }
      ]
    }
  ]
});
