<template>
  <div id="main-app" class="transparent">
    <div id="steps" class="row">
      <template v-if="showwhite">
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default;min-width: 35px">1</button>
          <span class="step-text center">{{multilanguage.configureservice[language]}}</span>
        </div>
        <div class="col-3">
          <button
            class="btn btn-dark rounded-circle"
