          >2</button>
          <a style="cursor: pointer;" @click="goback('dispo')">
            <span class="step-text center">{{multilanguage.selectservice[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default;min-width: 35px">3</button>
          <span class="step-text center">{{multilanguage.configureservice[language]}}</span>
        </div>
        <div class="col-3">
          <button
            class="btn btn-dark rounded-circle"
            disabled="disabled"
            style="cursor: default;min-width: 35px"
          >4</button>
          <span class="step-text center">{{multilanguage.fillcheckbook[language]}}</span>
        </div>
      </template>
    </div>
    <div class="row">
      <div class="col-12 mt-5">
        <div class="othertitle">{{title}}</div>
